// material-ui
import { styled } from '@mui/material/styles';
import { Grid, Table, TableBody, TableCell, Stack, tableCellClasses, Typography, IconButton, Menu, MenuItem, CardContent, Button, TextField, TableContainer, TableHead, TableRow, Tooltip, Fab } from '@mui/material';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
const DivValue = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    paddingRight: 12,
    paddingTop: 2,
    fontSize: 16,
}));
const DivTitle = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    color: theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.common.black,
    paddingTop: 2,
    paddingLeft: 12,
    fontSize: 16,
    fontWeight: 400
}));
// styles
const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14
    }
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
    },
    // hide last border
    '&:last-of-type td, &:last-of-type th': {
        border: 0
    }
}));

// table data
function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
}

