export default function InterimInvoiceDetail() {
    return (
        <MainCard
            content={false}
            title="Transfer Data Detail"
        >
            <Grid container alignItems="center" justifyContent="flex-start" spacing={2}>
                <Grid item xs={12} sm={6}>
                    <Grid
                        container
                        direction="row"
                        justifyContent="flex-start"
                        alignItems="flex-start"
                        mb={1}
                        mt={4}
                    >
                        <Grid item xs="1"></Grid>
                        <Grid item align="start">
                            <DivTitle>Date</DivTitle>
                        </Grid>
                        <Grid item pl={2}>
                            <DivValue> 2023 / 11 / 02 - 10 : 20 PM</DivValue>
                        </Grid>
                        <Grid item pl={5}>
                            <DivValue> 5069</DivValue>
                        </Grid>
                    </Grid>
                    <Grid
                        container
                        direction="row"
                        justifyContent="flex-start"
                        alignItems="flex-end"
                        mb={4}
                        ml={1}
                    >
                        <Grid item xs={1}></Grid>
                        <Grid item xs={11}>
                            <Typography variant='h1'>STORE TRANSFER</Typography>
                        </Grid>
                    </Grid>
                    <Grid
                        container
                        direction="row"
                        justifyContent="flex-start"
                        alignItems="flex-end"
                    >
                        <Grid item xs="1"></Grid>
                        <Grid item align="start">
                            <DivTitle>FROM: </DivTitle>
                        </Grid>
                        <Grid item pl={2}>
                            <DivValue> EL-ALFY 1</DivValue>
                        </Grid>
                    </Grid>
                    <Grid
                        container
                        direction="row"
                        justifyContent="flex-start"
                        alignItems="flex-end"
                        mb={1}
                    >
                        <Grid item xs="1"></Grid>
                        <Grid item align="start">
                            <DivTitle>TO: </DivTitle>
                        </Grid>
                        <Grid item pl={2}>
                            <DivValue> EL-ALFY 1</DivValue>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            <TableContainer>
                <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell align='center' colSpan={6}></StyledTableCell>
                            <StyledTableCell align='center' colSpan={2}>Size(M2)</StyledTableCell>
                            <StyledTableCell align='center'>Area of each slap(M2)</StyledTableCell>
                            <StyledTableCell align='center'>Total Area</StyledTableCell>
                            <StyledTableCell align='center'>Price</StyledTableCell>
                            <StyledTableCell align='center'>Total Price</StyledTableCell>
                            <StyledTableCell align='center'></StyledTableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell align='center'>Item</TableCell>
                            <TableCell align='center'>Producr Name</TableCell>
                            <TableCell align='center'>Code</TableCell>
                            <TableCell align='center'>Unit</TableCell>
                            <TableCell align='center'>Teck.(CM)</TableCell>
                            <TableCell align='center'>Quantity</TableCell>
                            <TableCell align='center'>W</TableCell>
                            <TableCell align='center'>H</TableCell>
                            <TableCell align='center'>M2</TableCell>
                            <TableCell align='center'>M2</TableCell>
                            <TableCell align='center'>L.E</TableCell>
                            <TableCell align='center'>L.E</TableCell>
                            <TableCell align='center'>Picture</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {newRow.map((row) => (
                            <TableRow hover key={row.id}>
                                {Object.keys(row).map((item, i) => (
                                    <>
                                        {item != "id" && (
                                            <TableCell align="center" key={i}>{row[item]}</TableCell>
                                        )}
                                    </>
                                ))}
                                <TableCell align="center"></TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </MainCard>
    );
}
